// Theme variables are globally available via CSS custom properties

// RadioButton component styles
.radio-group-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  
  label {
    font-weight: 500;
    color: var(--form-text-primary);
    margin-bottom: 10px;
  }
  
  .mat-mdc-radio-group {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
  
  .mat-mdc-radio-button {
    margin-bottom: 5px;
  }
}
